.SearchBar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    border: 1px solid rgba(0,0,0,0.2);
    width: 480px;
    height: 25px;
    padding: 2px;
}

.SearchBar-icon {
    width: 15px;
    padding: 0 5px;
    opacity: 0.3;
}

.SearchBar-input {
    width: 100%;
    height: 100%;
    border: 0;
    background: 0;
    font-size: 16px;
    font-family: 'Lato', sans-serif;

}

.SearchBar-input:focus {
    outline: 0;
}



