.TopNav {
    grid-column: span 2;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    padding: 30px 0;
}

.TopNav-logo {
    -ms-flex-preferred-size: 20%;
        flex-basis: 20%;
    font-size: 24px;
    font-weight: 300;   
}

.TopNav-logo-text {
    display: inline-block;
    padding: 5px;
    background: #FF4D4D;
    color: white;
    text-transform: uppercase;
    text-decoration: none;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    -webkit-animation: LogoZoomIn 1.2s;
            animation: LogoZoomIn 1.2s;
}

.TopNav-logo-text:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}


.TopNav-logo-text span {
    font-weight: 700; 
}

@-webkit-keyframes LogoZoomIn {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    50% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5)
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1)
    }
}

@keyframes LogoZoomIn {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    50% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5)
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1)
    }
}


