.NewsItem {
    display: -ms-flexbox;
    display: flex;
    padding: 20px 0;
    -webkit-animation-name: NewsItemFadeIn;
            animation-name: NewsItemFadeIn;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

.NewsItem-options {
    -ms-flex-preferred-size: 75px;
        flex-basis: 75px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.NewsItem-votes {
    display: -ms-flexbox;
    display: flex;
}

.NewsItem-vote-icon {
    height: 25px;
    width: 25px;
    opacity: 0.8;
}

.NewsItem-vote-count {
    opacity: 0.8;
}

.NewsItem-responses {
    display: -ms-flexbox;
    display: flex;
}

.NewsItem-responses-icon {
    height: 20px;
    width: 20px;
    padding: 0 3px 0 2px;
    opacity: 0.3;
}

.NewsItem-responses-count {
    font-size: 14px;
    opacity: 0.8;
}

.NewsItem-title {
    font-weight: 400;
    font-size: 18px;
    font-weight: 300;
    color: black;
    letter-spacing: 0.5px;
    text-decoration: none;
}

.NewsItem-desc {
    padding: 12px 0;
    font-size: 14px;
    opacity: 0.5;   
}

.NewsItem-information {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.NewsItem-meta-info {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding: 10px 0;
    font-size: 12px;
    font-weight: 300;
    letter-spacing: 0.5px;
    opacity: 0.7;
}

.NewsItem-user {
    padding-right: 10px;
    padding-bottom: 8px;
    padding-left: 5px;
    color: black;
    text-decoration: none;  
}

.NewsItem-tag {
    padding: 2px 4px;
    margin: 0 4px 4px 0;
    display: inline-block;
    text-transform: uppercase;
    text-decoration: none;
    color: #888;
    border: 1px solid #888;   
}

@-webkit-keyframes NewsItemFadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes NewsItemFadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}


